@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";





































.module-page-head {
  @include margin-md-b;

  .head_title,
  .head_btn {
    display: inline-block;
    vertical-align: middle;
  }

  .head_title {
    @include font-xl-b;
    @include media-breakpoint-up-md {
      @include font-3xl-b;
    }
  }
  .head_btn {
    @include font-lg;
    @include margin-md-l;
    color: $color-gray-d;
    cursor: pointer;

    &.btn-github:hover {
      color: $color-github;
    }
    &.btn-npm:hover {
      color: $color-npm;
    }
  }
}
