@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";





















.module-divider {
  @include margin-md-y;
  @include opacity-50;
  display: flex;
  flex-direction: row;
  align-items: center;

  .divider_line {
    @include margin-x(0.25rem);
    @include border-dashed-b(1px);
    border-color: $color-gray;
    width: 100%;
    height: 1px;
    flex-grow: 1;
  }

  .divider_icon {
    @include margin-x(0.25rem);
    @include font-xs;
    flex-shrink: 0;
  }
}
