@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";




















.module-section {
  & + .module-section {
    @include margin-t(1.5rem);
  }

  .section_head {
    @include font-sm-b;
    @include border-solid-b(1px);
    @include margin-lg-b;
    @include padding-sm-b;
    border-color: $color-gray-l;
    color: $color-gray-l;
  }
  .section_body {
    @include margin-lg-l;
  }
}
