@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";













.module-pre {
  @include padding-md-y;
  @include padding-lg-x;
  padding: 0.5rem 1rem;
  background-color: $color-gray-ll;
  overflow: auto;

  pre {
    margin: 0;
  }

  .pre_notice {
    color: $color-gray-l;
  }
  .pre_typeof {
    @include padding-md-x;
    color: $color-primary;
    background-color: $color-gray-ddd;
  }
}
